export function Nothing() {
    return { kind: 'Nothing' };
}
export function Just(value) {
    return { kind: 'Just', value: value };
}
export function isJust(maybe) {
    return (maybe.kind === 'Just');
}
export function justs(maybes) {
    let result = [];
    for (let maybe of maybes) {
        if (isJust(maybe)) {
            result.push(maybe.value);
        }
    }
    return result;
}
export function maximum(xs) {
    let result = Nothing();
    for (let x of xs) {
        if (isJust(result)) {
            result = Just(Math.max(result.value, x));
        }
        else {
            result = Just(x);
        }
    }
    return result;
}
